<template class="template">
  <rise-loader
    class="rise__loader"
    :loading="productLoadingDelayed"
    :size="'20px'"
  ></rise-loader>
  <transition name="fade">
    <div
      class="content__title preload-text"
      v-if="!productData && !productLoading"
    >
         
    </div>
  </transition>

  <div class="content container">
    <transition name="fade">
      <div class="content__top" v-if="productData">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link
              class="breadcrumbs__link"
              :to="{ name: 'main', query: { categoryId: category.id } }"
            >
              {{ category.title }}
            </router-link>
          </li>

          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> {{ product.title }} </a>
          </li>
        </ul>
      </div>
    </transition>
    <transition name="fade">
      <section class="item" v-if="productData">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img
              v-if="product.colors[selectedColorIndex].gallery"
              width="570"
              height="570"
              :src="product.colors[selectedColorIndex].gallery[0].file.url"
              :alt="product.title"
            />
            <div v-else width="570" height="570">
                "{{ product.title }}" 
            </div>
          </div>
        </div>

        <div class="item__info">
          <span class="item__code">: {{ product.id }} </span>
          <h2 class="item__title">{{ product.title }}</h2>
          <div class="item__form">
            <form
              class="form"
              action="#"
              method="POST"
              @submit.prevent="addToCart"
            >
              <div class="item__row item__row--center">
                <product-counter
                  :total-amount="100"
                  :start-amount="1"
                  v-on:update-amount="productAmount = $event"
                ></product-counter>

                <b class="item__price"> {{ numberFormat(product.price) }}  </b>
              </div>

              <div class="item__row">
                <fieldset class="form__block">
                  <legend class="form__legend"></legend>
                  <ul class="colors colors--black">
                    <li
                      class="colors__item"
                      v-for="color in product.colors"
                      :key="color.color.id"
                    >
                      <label class="colors__label">
                        <input
                          class="colors__radio sr-only"
                          type="radio"
                          name="color-item"
                          :value="color.color.id"
                          v-model="selectedColorId"
                        />
                        <span
                          class="colors__value"
                          :style="{ backgroundColor: color.color.code }"
                        >
                        </span>
                      </label>
                    </li>
                  </ul>
                </fieldset>

                <fieldset class="form__block">
                  <legend class="form__legend"></legend>
                  <label
                    class="form__label form__label--small form__label--select"
                  >
                    <select
                      class="form__select"
                      name="category"
                      v-model="selectedSizeId"
                    >
                      <option
                        :value="size.id"
                        v-for="size in product.sizes"
                        :key="size.id"
                      >
                        {{ size.title }}
                      </option>
                    </select>
                  </label>
                </fieldset>
              </div>

              <button
                class="item__button button button--primery"
                type="submit"
                :disabled="productAddSending"
              >
                <beat-loader
                  :loading="productAddSending"
                  :size="'7px'"
                ></beat-loader>
                <span v-if="!productAdded && !productAddSending"
                  > </span
                >
                <span v-if="productAdded">!</span>
              </button>
              <!-- <base-modal v-model:open="isShowAddedMessage" to="#teleport-target"
              >   </base-modal
            > -->
            </form>
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <div
                class="tabs__link"
                :class="{ 'tabs__link--current': selectedTabFirst }"
                @click="selectedTabFirst = true"
              >
                  
              </div>
            </li>

            <li class="tabs__item">
              <div
                class="tabs__link"
                :class="{ 'tabs__link--current': !selectedTabFirst }"
                @click="selectedTabFirst = false"
              >
                  
              </div>
            </li>
          </ul>

          <transition name="fade">
            <div v-if="selectedTabFirst" class="item__content">
              <h3>:</h3>

              <p>
                60% <br />
                30% <br />
              </p>

              <h3>:</h3>

              <p>
                   . 30C  <br />
                  . 110C<br />
                   <br />
                 <br />
                  <br />
              </p>
            </div>
          </transition>

          <transition name="fade">
            <div v-if="!selectedTabFirst" class="item__content">
              <h3>:</h3>

              <p>
                1.         290 <br />
                2.  .      
                 <br />
              </p>

              <h3>:</h3>

              <p>
                        
                 .<br />
                      .   
                  ,     .
                     .<br />
                          
                ,    8 800 600 90 09<br />
              </p>
            </div>
          </transition>
        </div></section
    ></transition>
  </div>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import ProductCounter from "@/components/ProductCounter.vue";
import { mapActions } from "vuex";
// import BaseModal from "@/components/BaseModal.vue";
import { loadProduct } from "@/api";
import RiseLoader from "vue-spinner/src/RiseLoader.vue";
import BeatLoader from "vue-spinner/src/BeatLoader.vue";

export default {
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingDelayed: false,

      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false,

      isShowAddedMessage: false,

      selectedColorId: null,
      selectedSizeId: null,

      selectedTabFirst: true,
    };
  },
  components: {
    ProductCounter,
    BeatLoader,
    RiseLoader,
    // BaseModal,
  },
  computed: {
    product: function () {
      return this.productData;
    },
    category: function () {
      return this.productData.category;
    },
    selectedColorIndex() {
      if (this.selectedColorId) {
        return this.product.colors.findIndex(
          (c) => c.color.id === this.selectedColorId
        );
      } else {
        return 0;
      }
    },
  },
  methods: {
    ...mapActions(["addProductToCart"]),
    numberFormat,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
        colorId: this.selectedColorId,
        sizeId: this.selectedSizeId,
      }).then(() => {
        this.isShowAddedMessage = true;
        this.productAdded = true;
        this.productAddSending = false;
      });
    },
    getProduct() {
      this.productLoading = true;
      let timeoutId;
      timeoutId = setTimeout(() => {
        this.productLoadingDelayed = true;
      }, 500);

      this.productLoadingFailed = false;
      loadProduct(this.$route.params.id)
        .then((response) => {
          this.productData = response.data;
          this.selectedSizeId = this.productData.sizes[0].id;
          this.selectedColorId = this.productData.colors[0].color.id;
        })
        .catch(() => (this.productLoadingFailed = true))
        .then(() => {
          this.productLoading = false;
          this.productLoadingDelayed = false;

          clearTimeout(timeoutId);
        });
    },
  },
  created() {
    this.getProduct();
  },
  watch: {
    "this.$route.params.id"() {
      this.getProduct();
    },
    productAmount() {
      this.productAdded = false;
    },
    selectedColorId() {
      this.productAdded = false;
    },
    selectedSizeId() {
      this.productAdded = false;
    },
  },
};
</script>

<style scoped>
.container {
  min-height: calc(100vh - 408px);
}

.preload-text {
  margin: 50px auto;
  font-weight: bold;
  text-align: center;
}
.tabs__link:not(.tabs__link--current) {
  cursor: pointer;
}
.template {
  position: relative;
}
.rise__loader {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fade-enter-active {
  transition: opacity 1s ease;
}
.fade-enter-from {
  opacity: 0;
}
</style>
