<template>
  <div class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title"></h1>
        <span class="content__info">
          {{ pluralize(countProducts, ["", "", ""]) }}
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <product-filter
        v-model:price-from="filterPriceFrom"
        v-model:price-to="filterPriceTo"
        v-model:category-id="filterCategoryId"
        v-model:material-ids="filterMaterialIds"
        v-model:season-ids="filterSeasonIds"
        v-model:color-ids="filterColorIds"
        :products="productsDataNotPaginated"
      ></product-filter>

      <section class="catalog">
        <div class="template-load-failed">
          <rise-loader
            class="rise__loader"
            :loading="productsLoading"
            :size="'20px'"
          ></rise-loader>
          <transition name="fade">
            <div
              v-if="productsLoadingFailed"
              class="content__title preload-text"
            >
                  ...
            </div></transition
          >
          <transition name="fade">
            <button
              class="button button--primery button__preload"
              v-if="productsLoadingFailed"
              @click.prevent="getProducts"
            >
                
            </button>
          </transition>
        </div>
        <transition name="fade" appear>
          <product-list
            :products="products"
            v-if="products.length > 0"
          ></product-list>
        </transition>

        <transition name="fade">
          <base-pagination
            v-if="pagination.pages > 1"
            v-model="page"
            :count="countProducts"
            :per-page="productsPerPage"
          ></base-pagination
        ></transition>
      </section>
    </div>
  </div>
</template>

<script>
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import { loadProducts } from "@/api";
import ProductList from "@/components/ProductList.vue";
import pluralize from "@/helpers/pluralize";
import RiseLoader from "vue-spinner/src/RiseLoader.vue";

export default {
  components: { BasePagination, ProductFilter, ProductList, RiseLoader },
  data() {
    return {
      page: 1,
      productsPerPage: 12,

      filterPriceFrom: null,
      filterPriceTo: null,
      filterCategoryId: null,
      filterMaterialIds: [],
      filterSeasonIds: [],
      filterColorIds: [],

      productsData: null,
      productsDataNotPaginated: [],

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
    pagination() {
      return this.productsData ? this.productsData.pagination : {};
    },
    filterPropsArray() {
      return [
        this.filterPriceFrom,
        this.filterPriceTo,
        this.filterCategoryId,
        this.filterMaterialIds,
        this.filterSeasonIds,
        this.filterColorIds,
      ];
    },
  },
  watch: {
    page() {
      this.getProducts();
    },
    filterPropsArray() {
      this.getProducts();
      this.page = 1;
    },
    $route() {
      if (this.$route.query.categoryId) {
        this.filterCategoryId = Number(this.$route.query.categoryId);
      } else {
        this.filterCategoryId = 0;
      }
    },
  },
  methods: {
    pluralize,
    getProducts() {
      this.productsData = null;

      let timeoutId;

      timeoutId = setTimeout(() => {
        this.productsLoading = true;
      }, 500);

      this.productsLoadingFailed = false;
      loadProducts(
        this.page,
        this.productsPerPage,
        this.filterPriceFrom,
        this.filterPriceTo,
        this.filterCategoryId,
        this.filterMaterialIds,
        this.filterSeasonIds,
        this.filterColorIds
      )
        .then((response) => (this.productsData = response.data))
        .catch(() => (this.productsLoadingFailed = true))
        .then(() => {
          this.productsLoading = false;
          clearTimeout(timeoutId);
        });
      loadProducts(
        null,
        null,
        this.filterPriceFrom,
        this.filterPriceTo,
        this.filterCategoryId,
        this.filterMaterialIds,
        this.filterSeasonIds,
        this.filterColorIds
      ).then(
        (response) => (this.productsDataNotPaginated = response.data.items)
      );
    },
  },
  created() {
    this.$nextTick(() => {
      if (this.$route.query.categoryId) {
        this.filterCategoryId = Number(this.$route.query.categoryId);
      } else {
        this.filterCategoryId = 0;
      }
    });
  },
};
</script>

<style scoped>
.container {
  min-height: calc(100vh - 408px);
}

.template-load-failed {
  position: relative;
  display: flex;
  flex-direction: column;
}
.rise__loader {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.preload-text {
  margin: 50px auto;
  font-weight: bold;
  text-align: center;
}
.button__preload {
  margin-right: auto;
  margin-left: auto;
  max-width: max-content;
}
.fade-enter-active {
  transition: opacity 1s ease;
}
.fade-enter-from {
  opacity: 0;
}
</style>
