<template>
  <label class="form__label">
    <slot></slot>
    <span class="form__value"> {{ title }} </span>
    <transition name="fade">
      <span class="form__error" v-if="error">
        {{ error }}
      </span></transition
    >
  </label>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: false,
    },
    error: {
      type: String,
      required: false,
    },
  },
};
</script>

<style scoped>
.fade-enter-active {
  transition: opacity 0.5s ease;
}
.fade-enter-from {
  opacity: 0;
}
</style>
