<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">  </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title"></h1>
        <span class="content__info">
          {{
            pluralize($store.state.cartProducts.length, [
              "",
              "",
              "",
            ])
          }}
        </span>
      </div>
    </div>

    <rise-loader
      class="rise__loader"
      :loading="$store.state.cartProductsLoading"
      :size="'20px'"
    ></rise-loader>
    <transition name="fade" appear>
      <section class="cart" v-if="!$store.state.cartProductsLoading">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list">
              <cart-item
                v-for="item in products"
                :item="item"
                :key="item.productId"
              ></cart-item>
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
                    
            </p>
            <p class="cart__price">
              : <span> {{ numberFormat(totalPrice) }} </span>
            </p>

            <router-link v-slot="{ navigate }" :to="{ name: 'order' }" custom>
              <button
                class="cart__button button button--primery"
                type="button"
                @click="navigate"
                :disabled="$store.state.cartProducts.length < 1"
              >
                 
              </button>
            </router-link>
          </div>
        </form>
      </section></transition
    >
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat.js";
import { mapGetters } from "vuex";
import CartItem from "@/components/CartItem.vue";
import pluralize from "@/helpers/pluralize";
import RiseLoader from "vue-spinner/src/RiseLoader.vue";

export default {
  components: { CartItem, RiseLoader },
  methods: {
    numberFormat,
    pluralize,
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
  },
};
</script>

<style scoped>
.container {
  min-height: calc(100vh - 408px);
}
.preloader-text {
  text-align: center;
}
.fade-enter-active {
  transition: opacity 1s ease;
}
.fade-enter-from {
  opacity: 0;
}
</style>
