<template>
  <div class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title"> </h1>
      </div>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <base-form-text
              v-model="formData.name"
              :error="formError.name"
              title=""
              placeholder="   "
            ></base-form-text>

            <base-form-text
              v-model="formData.address"
              :error="formError.address"
              title=" "
              placeholder="  "
            ></base-form-text>

            <base-form-text
              v-model="formData.phone"
              :error="formError.phone"
              title=""
              placeholder="  "
              type="tel"
            ></base-form-text>

            <base-form-text
              v-model="formData.email"
              :error="formError.email"
              title="Email"
              placeholder="  Email"
              type="email"
            ></base-form-text>

            <base-form-textarea
              v-model="formData.comment"
              title="  "
              :error="formError.comment"
              placeholder=" "
            ></base-form-textarea>
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>

            <transition name="fade" appear>
              <ul class="cart__options options" v-if="deliveresData">
                <li
                  class="options__item"
                  v-for="delivery in deliveresData"
                  :key="delivery.id"
                >
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="delivery"
                      :value="delivery.id"
                      v-model="formData.deliveryTypeId"
                    />
                    <span class="options__value">
                      {{ delivery.title }}
                      <b> {{ formatDeliveryPrice(delivery.price) }} </b>
                    </span>
                  </label>
                </li>
              </ul>
            </transition>
            <transition name="fade">
              <div
                class="cart__error form__error-block"
                v-if="getDeliveresFailed"
              >
                <h4>     ...</h4>
                <p>  .</p>
              </div></transition
            >
            <rise-loader
              class="rise__loader"
              :loading="deliveresLoading"
              :size="'20px'"
            ></rise-loader>

            <h3 class="cart__title"></h3>
            <transition name="fade" appear>
              <ul class="cart__options options" v-if="paymentsData">
                <li
                  class="options__item"
                  v-for="payment in paymentsData"
                  :key="payment.id"
                >
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="pay"
                      :value="payment.id"
                      v-model="formData.paymentTypeId"
                    />
                    <span class="options__value"> {{ payment.title }} </span>
                  </label>
                </li>
              </ul></transition
            >
            <transition name="fade">
              <div
                class="cart__error form__error-block"
                v-if="getPaymentsFailed"
              >
                <h4>     ...</h4>
                <p>  .</p>
              </div></transition
            >

            <rise-loader
              class="rise__loader"
              :loading="paymentsLoading"
              :size="'20px'"
            ></rise-loader>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="item in products"
              :item="item"
              :key="item.productId"
            >
              <h3>{{ item.product.title }}</h3>
              <b>{{ numberFormat(item.product.price) }} </b>
              <span>: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b></b></p>
            <p>
              : <b> {{ products.length }} </b>   
              <b
                >{{
                  numberFormat(Number(totalPrice) + Number(deliveryPrice))
                }}
                </b
              >
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            <beat-loader :loading="dataIsSending" :size="'7px'"></beat-loader>
            <span v-if="!dataIsSending"> </span>
          </button>
        </div>
        <transition name="fade">
          <div class="cart__error form__error-block" v-if="formErrorMessage">
            <h4>  !</h4>
            <p>
              {{ formErrorMessage }}
            </p>
          </div></transition
        >
        <transition name="fade">
          <div class="cart__error form__error-block" v-if="dataIsSending">
            <h4>     ...</h4>
            <p>, .</p>
          </div></transition
        >
      </form>
    </section>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
import numberFormat from "@/helpers/numberFormat.js";
import { sendOrder, loadDeliveres, loadPayments } from "@/api";
import RiseLoader from "vue-spinner/src/RiseLoader.vue";
import BeatLoader from "vue-spinner/src/BeatLoader.vue";

export default {
  components: {
    BaseFormText,
    BaseFormTextarea,
    RiseLoader,
    BeatLoader,
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: "",
      dataIsSending: false,

      deliveresData: null,
      paymentsData: null,

      deliveresLoading: false,
      paymentsLoading: false,
      getDeliveresFailed: false,
      getPaymentsFailed: false,
    };
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
    deliveryPrice() {
      if (this.deliveresData) {
        return this.deliveresData.find(
          (delivery) => delivery.id === this.formData.deliveryTypeId
        ).price;
      } else {
        return 0;
      }
    },
  },
  methods: {
    numberFormat,
    formatDeliveryPrice(price) {
      if (price > 0) {
        return numberFormat(price) + " ";
      } else {
        return "";
      }
    },
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      this.dataIsSending = true;

      setTimeout(() => {
        sendOrder(this.formData, this.$store.state.userAccessKey)
          .then((response) => {
            this.dataIsSending = false;
            this.$store.commit("resetCart");
            this.$store.commit("updateOrderInfo", response.data);
            this.$router.push({
              name: "orderInfo",
              params: { id: response.data.id },
            });
          })
          .catch((error) => {
            this.dataIsSending = false;
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage =
              error.response.data.error.message ||
              error.response.data.error.request.paymentTypeId;
          });
      }, 300);
    },
    getDeliveres() {
      let timeoutId;
      timeoutId = setTimeout(() => {
        this.deliveresLoading = true;
      }, 500);

      loadDeliveres()
        .then((response) => {
          this.deliveresData = response.data;
          this.formData.deliveryTypeId = response.data[0].id;
          this.getPayments(this.formData.deliveryTypeId);
          this.deliveresLoading = false;
          clearInterval(timeoutId);
        })
        .catch(() => {
          this.getDeliveresFailed = true;
        });
    },
    getPayments(id) {
      let timeoutId;
      timeoutId = setTimeout(() => {
        this.paymentsLoading = true;
      }, 500);

      loadPayments(id)
        .then((response) => {
          this.paymentsData = response.data;
          this.formData.paymentTypeId = response.data[0].id;
          this.paymentsLoading = false;
          clearInterval(timeoutId);
        })
        .catch(() => {
          this.getPaymentsFailed = true;
        });
    },
  },
  created() {
    this.getDeliveres();
  },
};
</script>

<style scoped>
.container {
  min-height: calc(100vh - 408px);
}
.fade-enter-active {
  transition: opacity 1s ease;
}
.fade-enter-from {
  opacity: 0;
}
</style>
