import { createStore } from "vuex";
import {
  loadOrderInfoFromApi,
  loadCartFromApi,
  addProductToCartToApi,
  updateCartProductAmountToApi,
  deleteCartProductFromApi,
} from "@/api";

const store = createStore({
  state: {
    cartProducts: [],
    cartProductsLoading: false,

    userAccessKey: null,
    cartProductsData: [],

    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find(
        (item) => item.cartItemId === productId
      );
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter(
        (item) => item.productId !== productId
      );
    },
    updateUserAccesKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          productId: item.product.id,
          amount: item.quantity,
          color: item.color.color.code,
          colorId: item.color.color.id,
          colorTitle:
            item.color.color.title[0].toUpperCase() +
            item.color.color.title.substring(1),
          cartItemId: item.id,
        };
      });
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const product = state.cartProductsData.find(
          (p) => p.product.id === item.productId
        ).product;
        return {
          ...item,
          product,
        };
      });
    },
    cartTotalPrice(state, getters) {
      return (
        Math.ceil(
          getters.cartDetailProducts.reduce(
            (acc, item) => item.product.price * item.amount + acc,
            0
          ) * 100
        ) / 100
      );
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return loadOrderInfoFromApi(orderId, context.state.userAccessKey).then(
        (response) => {
          context.commit("updateOrderInfo", response.data);
        }
      );
    },
    loadCart(context) {
      let timeoutId;
      timeoutId = setTimeout(() => {
        context.state.cartProductsLoading = true;
      }, 500);

      return loadCartFromApi(context.state.userAccessKey)
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem("userAccesKey", response.data.user.accessKey);
            context.commit("updateUserAccesKey", response.data.user.accessKey);
          }
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        })
        .then(() => {
          context.state.cartProductsLoading = false;
          clearInterval(timeoutId);
        });
    },
    addProductToCart(context, { productId, amount, colorId, sizeId }) {
      return addProductToCartToApi(
        { productId, amount, colorId, sizeId },
        context.state.userAccessKey
      ).then((response) => {
        context.commit("updateCartProductsData", response.data.items);
        context.commit("syncCartProducts");
      });
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit("updateCartProductAmount", {
        productId,
        amount,
      });

      if (amount < 1) {
        return;
      }
      return updateCartProductAmountToApi(
        { productId, amount },
        context.state.userAccessKey
      )
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
        })
        .catch(() => {
          context.commit("syncCartProducts");
        });
    },
    deleteCartProduct(context, { productId }) {
      return deleteCartProductFromApi(productId, context.state.userAccessKey)
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        })
        .catch(() => {
          context.commit("syncCartProducts");
        });
    },
  },
});

export default store;
