<template>
  <div class="form__counter">
    <button
      type="button"
      aria-label="  "
      @click.prevent="decreaseProductAmount"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input
      type="text"
      name="count"
      v-model.number="productAmount"
      @input.prevent="setProductAmount"
    />

    <button
      type="button"
      aria-label="  "
      @click.prevent="increaseProductAmount"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: { totalAmount: { type: Number }, startAmount: { type: Number } },
  emits: { "update-amount": (value) => typeof value === "number" },
  data() {
    return {
      productAmount: 1,
    };
  },
  methods: {
    increaseProductAmount() {
      if (this.productAmount < this.totalAmount) {
        this.productAmount++;
      }
      this.$emit("update-amount", this.productAmount);
    },
    decreaseProductAmount() {
      if (this.productAmount > 1) {
        this.productAmount--;
      }
      this.$emit("update-amount", this.productAmount);
    },
    setProductAmount() {
      if (this.productAmount <= this.totalAmount) {
        this.$emit("update-amount", this.productAmount);
      } else {
        this.$emit("update-amount", this.totalAmount);
      }
    },
  },
  created() {
    this.productAmount = this.startAmount;
  },
};
</script>
