<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        v-if="item.product.colors[selectedColorIndex].gallery"
        :src="item.product.colors[selectedColorIndex].gallery[0].file.url"
        width="120"
        height="120"
        :alt="item.product.title"
      />
      <div v-else width="120" height="120">
          "{{ item.product.title }}" 
      </div>
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <p class="product__info product__info--color">
      :
      <span>
        <i :style="{ backgroundColor: item.color }"></i>
        {{ item.colorTitle }}
      </span>
    </p>
    <span class="product__code"> : {{ item.product.id }} </span>

    <product-counter
      :total-amount="100"
      :start-amount="amount"
      @update-amount="amount = $event"
    ></product-counter>

    <b class="product__price">
      {{ numberFormat(item.amount * item.product.price) }} 
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="   "
      @click.prevent="deleteProduct(item.productId)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from "@/helpers/numberFormat.js";
import ProductCounter from "@/components/ProductCounter.vue";

export default {
  props: {
    item: { type: Object },
  },
  components: {
    ProductCounter,
  },
  methods: {
    numberFormat,
    deleteProduct() {
      this.$store.dispatch("deleteCartProduct", {
        productId: this.item.cartItemId,
      });
    },
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch("updateCartProductAmount", {
          productId: this.item.cartItemId,
          amount: value,
        });
      },
    },

    selectedColorIndex() {
      if (this.item.product.colors) {
        return this.item.product.colors.findIndex(
          (c) => c.color.id === this.item.colorId
        );
      } else {
        return 0;
      }
    },
  },
};
</script>

<style scoped>
.button-del {
  cursor: pointer;
}
</style>
