<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <img
        v-if="product.colors[selectedColorIndex].gallery"
        :src="product.colors[selectedColorIndex].gallery[0].file.url"
        :alt="product.title"
      />
      <p v-else class="item__no-img">
          "{{ product.title }}" 
      </p>
    </router-link>

    <h3 class="catalog__title" @click.prevent="openQuickView()">
      <a> {{ product.title }} </a>
    </h3>

    <span class="catalog__price"> {{ numberFormat(product.price) }}  </span>

    <ul class="colors colors--black">
      <li v-for="color in product.colors" :key="color.id" class="colors__item">
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            name="color-1"
            :value="color.color.id"
            v-model="selectedColorId"
          />
          <span
            class="colors__value"
            :style="{ backgroundColor: color.color.code }"
          >
          </span>
        </label>
      </li>
    </ul>
  </li>
  <base-modal v-model:open="isQuickViewOpen" to="#teleport-target">
    <product-quick-view :item-id="product.id"></product-quick-view>
  </base-modal>
</template>

<script>
import numberFormat from "../helpers/numberFormat";
import BaseModal from "@/components/BaseModal.vue";
import ProductQuickView from "./ProductQuickView.vue";

export default {
  components: { BaseModal, ProductQuickView },
  props: { product: { type: Object } },
  data() {
    return {
      selectedColorId: null,
      isQuickViewOpen: false,
    };
  },
  computed: {
    selectedColorIndex() {
      if (this.selectedColorId) {
        return this.product.colors.findIndex(
          (c) => c.color.id === this.selectedColorId
        );
      } else {
        return 0;
      }
    },
  },
  methods: {
    numberFormat,
    openQuickView() {
      this.isQuickViewOpen = true;
    },
  },
};
</script>

<style scoped>
.item__no-img {
  text-align: center;
}
.catalog__title {
  cursor: pointer;
}
</style>
