import axios from "axios";

const API_BASE_URL = "https://vue-moire.skillbox.cc";

export const loadProducts = (
  page,
  limit,
  minPrice,
  maxPrice,
  categoryId,
  materialIds,
  seasonIds,
  colorIds
) => {
  return axios.get(API_BASE_URL + "/api/products", {
    params: {
      page: page,
      limit: limit,
      minPrice: minPrice,
      maxPrice: maxPrice,
      categoryId: categoryId,
      materialIds: materialIds,
      seasonIds: seasonIds,
      colorIds: colorIds,
    },
  });
};

export const loadCategories = () => {
  return axios.get(API_BASE_URL + "/api/productCategories");
};

export const loadMaterials = () => {
  return axios.get(API_BASE_URL + "/api/materials");
};

export const loadSeasons = () => {
  return axios.get(API_BASE_URL + "/api/seasons");
};

export const loadColors = () => {
  return axios.get(API_BASE_URL + "/api/colors");
};

export const loadDeliveres = () => {
  return axios.get(API_BASE_URL + "/api/deliveries");
};

export const loadPayments = (id) => {
  return axios.get(API_BASE_URL + "/api/payments", {
    params: {
      deliveryTypeId: id,
    },
  });
};

export const loadProduct = (id) => {
  return axios.get(API_BASE_URL + "/api/products/" + id);
};

export const sendOrder = (data, userAccessKey) => {
  return axios.post(
    API_BASE_URL + "/api/orders",
    {
      ...data,
    },
    {
      params: {
        userAccessKey: userAccessKey,
      },
    }
  );
};

// store

export const loadOrderInfoFromApi = (id, userAccessKey) => {
  return axios.get(API_BASE_URL + "/api/orders/" + id, {
    params: {
      userAccessKey: userAccessKey,
    },
  });
};

export const loadCartFromApi = (userAccessKey) => {
  return axios.get(API_BASE_URL + "/api/baskets", {
    params: {
      userAccessKey: userAccessKey,
    },
  });
};

export const addProductToCartToApi = (
  { productId, amount, colorId, sizeId },
  userAccessKey
) => {
  return axios.post(
    API_BASE_URL + "/api/baskets/products",
    {
      productId: productId,
      quantity: amount,
      colorId: colorId,
      sizeId: sizeId,
    },
    {
      params: {
        userAccessKey: userAccessKey,
      },
    }
  );
};

export const updateCartProductAmountToApi = (
  { productId, amount },
  userAccessKey
) => {
  return axios.put(
    API_BASE_URL + "/api/baskets/products",
    {
      basketItemId: productId,
      quantity: amount,
    },
    {
      params: {
        userAccessKey: userAccessKey,
      },
    }
  );
};

export const deleteCartProductFromApi = (id, userAccessKey) => {
  return axios.delete(API_BASE_URL + "/api/baskets/products", {
    data: {
      basketItemId: id,
    },
    params: {
      userAccessKey: userAccessKey,
    },
  });
};
