<template>
  <aside class="filter">
    <form
      class="filter__form form"
      action="#"
      method="get"
      v-on:submit.prevent="submit"
    >
      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            v-model.number="currentPriceFrom"
            placeholder="0"
          />
          <span class="form__value"></span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            v-model.number="currentPriceTo"
            placeholder=""
          />
          <span class="form__value"></span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <v-select
          class="select-custom"
          label="title"
          :value="categories.id"
          :options="categories"
          v-model.number="currentCategory"
          :clearable="false"
        ></v-select>
      </fieldset>

      <fieldset class="form__block add__height temp-color">
        <legend class="form__legend"></legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in this.colors" :key="color.id">
            <label class="colors__label">
              <input
                class="colors__radio sr-only"
                type="checkbox"
                name="color"
                :value="color.id"
                v-model="currentColorIds"
              />
              <span
                class="colors__value"
                :style="{ backgroundColor: color.code }"
              >
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="material in materials"
            :key="material.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="material"
                :value="material.id"
                v-model="currentMaterialIds"
              />
              <span class="check-list__desc">
                {{ material.title }}
                <span
                  >(
                  {{ categorySelectedAmount("materials", material.title) }}
                  )</span
                >
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="season in seasons"
            :key="season.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="collection"
                :value="season.id"
                v-model="currentSeasonIds"
              />
              <span class="check-list__desc">
                {{ season.title }}
                <span
                  >(
                  {{ categorySelectedAmount("seasons", season.title) }}
                  )</span
                >
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button
        class="filter__submit button button--primery"
        type="submit"
        :disabled="!resetButton"
      >
        
      </button>
      <transition name="fade">
        <button
          class="filter__reset button button--second"
          type="button"
          v-on:click.prevent="reset"
          v-if="resetButton"
        >
          
        </button></transition
      >
    </form>
  </aside>
</template>

<script>
import { loadCategories, loadColors, loadMaterials, loadSeasons } from "@/api";
import vSelect from "vue-select";
import "vue-select/dist/vue-select.css";

export default {
  components: { vSelect },
  data() {
    return {
      currentPriceFrom: null,
      currentPriceTo: null,
      currentCategory: {
        id: 0,
        title: " ",
        slug: "",
      },
      currentMaterialIds: [],
      currentSeasonIds: [],
      currentColorIds: [],

      categoriesData: null,
      materialsData: null,
      seasonsData: null,
      colorsData: null,
    };
  },
  props: {
    priceFrom: { type: Number },
    priceTo: { type: Number },
    categoryId: { type: Number },
    materialIds: { type: Array },
    seasonIds: { type: Array },
    colorIds: { type: Array },
    products: { type: Array },
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    currentCategoryId() {
      if (this.currentCategory) {
        return this.currentCategory.id;
      } else {
        return 0;
      }
    },
    materials() {
      return this.materialsData ? this.materialsData.items : [];
    },
    seasons() {
      return this.seasonsData ? this.seasonsData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
    resetButton() {
      return (
        !(
          this.currentPriceFrom === null ||
          this.currentPriceFrom === 0 ||
          this.currentPriceFrom === ""
        ) ||
        !(
          this.currentPriceTo === null ||
          this.currentPriceTo === 0 ||
          this.currentPriceTo === ""
        ) ||
        !(
          this.currentCategoryId === null ||
          this.currentCategoryId === 0 ||
          this.currentCategoryId === ""
        ) ||
        this.currentMaterialIds.length !== 0 ||
        this.currentSeasonIds.length !== 0 ||
        this.currentColorIds.length !== 0
      );
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategory = this.categories.find((cat) => cat.id === value);
    },
    materialIds(value) {
      this.currentMaterialIds = value;
    },
    seasonIds(value) {
      this.currentSeasonIds = value;
    },
    colorIds(value) {
      this.currentColorIds = value;
    },
  },
  methods: {
    submit() {
      this.$emit("update:priceFrom", this.currentPriceFrom);
      this.$emit("update:priceTo", this.currentPriceTo);
      this.$emit("update:categoryId", this.currentCategoryId);
      this.$emit("update:materialIds", this.currentMaterialIds);
      this.$emit("update:seasonIds", this.currentSeasonIds);
      this.$emit("update:colorIds", this.currentColorIds);
      this.$router.push({
        name: "main",
        query: { categoryId: this.currentCategoryId },
      });
    },
    reset() {
      this.$emit("update:priceFrom", null);
      this.$emit("update:priceTo", null);
      this.$emit("update:categoryId", 0);
      this.$emit("update:materialIds", []);
      this.$emit("update:seasonIds", []);
      this.$emit("update:colorIds", []);

      this.$nextTick(() => {
        this.currentCategory = {
          id: 0,
          title: " ",
          slug: "",
        };
      });

      this.$router.replace({ query: null });
    },
    getCategories() {
      loadCategories().then((response) => {
        this.categoriesData = response.data;

        if (this.categoryId === 0) {
          this.currentCategory = {
            id: 0,
            title: " ",
            slug: "",
          };
        } else {
          this.currentCategory = this.categories.find(
            (cat) => cat.id === this.categoryId
          );
        }
      });
    },
    getMaterials() {
      loadMaterials().then((response) => (this.materialsData = response.data));
    },
    getSeasons() {
      loadSeasons().then((response) => (this.seasonsData = response.data));
    },
    getColors() {
      loadColors().then((response) => (this.colorsData = response.data));
    },
    categorySelectedAmount(category, value) {
      if (this.products) {
        return this.products.filter((item) =>
          item[category].some((item) => item.title === value)
        ).length;
      } else {
        return 0;
      }
    },
  },
  created() {
    this.getCategories();
    this.getMaterials();
    this.getSeasons();
    this.getColors();
  },
};
</script>

<style scoped>
.colors__radio:checked ~ .colors__value::before {
  border-color: gray;
}
.fade-enter-active {
  transition: opacity 0.5s ease;
}
.fade-enter-from {
  opacity: 0;
}

.select-custom {
  padding: 14px;
  min-height: 52px;
  background-color: #fafafa;

  --vs-colors--lightest: rgba(60, 60, 60, 0.26);
  --vs-colors--light: rgba(60, 60, 60, 0.5);
  --vs-colors--dark: #333;
  --vs-colors--darkest: rgba(0, 0, 0, 0.15);

  /* Search Input */
  --vs-search-input-color: inherit;
  --vs-search-input-bg: rgb(255, 255, 255);
  --vs-search-input-placeholder-color: inherit;

  /* Font */
  --vs-font-size: 1rem;
  --vs-line-height: 1.4;

  /* Disabled State */
  --vs-state-disabled-bg: rgb(248, 248, 248);
  --vs-state-disabled-color: var(--vs-colors--light);
  --vs-state-disabled-controls-color: var(--vs-colors--light);
  --vs-state-disabled-cursor: not-allowed;

  /* Borders */
  --vs-border-color: var(--vs-colors--lightest);
  --vs-border-width: 0px;
  --vs-border-style: solid;
  --vs-border-radius: 0px;

  /* Actions: house the component controls */
  --vs-actions-padding: 0;

  /* Component Controls: Clear, Open Indicator */
  --vs-controls-color: var(--vs-colors--dark);
  --vs-controls-size: 0.7;
  --vs-controls--deselect-text-shadow: 0 1px 0 #fff;

  /* Selected */
  --vs-selected-bg: #f0f0f0;
  --vs-selected-color: var(--vs-colors--dark);
  --vs-selected-border-color: var(--vs-border-color);
  --vs-selected-border-style: var(--vs-border-style);
  --vs-selected-border-width: var(--vs-border-width);

  /* Dropdown */
  --vs-dropdown-bg: #fafafa;
  --vs-dropdown-color: inherit;
  --vs-dropdown-z-index: 1000;
  --vs-dropdown-min-width: 160px;
  --vs-dropdown-max-height: 350px;
  --vs-dropdown-box-shadow: none;

  /* Options */
  --vs-dropdown-option-bg: #000;
  --vs-dropdown-option-color: var(--vs-dropdown-color);
  --vs-dropdown-option-padding: 13px 18px;

  /* Active State */
  --vs-dropdown-option--active-bg: #f2e4ea;
  --vs-dropdown-option--active-color: var(--vs-colors--dark);

  /* Deselect State */
  --vs-dropdown-option--deselect-bg: #fb5858;
  --vs-dropdown-option--deselect-color: #fff;

  /* Transitions */
  --vs-transition-timing-function: cubic-bezier(1, -0.115, 0.975, 0.855);
  --vs-transition-duration: 150ms;
}
</style>
