<template>
  <router-link
    class="header__cart"
    href="cart.html"
    aria-label="  "
    :to="{ name: 'cart' }"
  >
    <svg width="19" height="24">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span class="header__count" aria-label=" ">
      {{ cartAmount }}
    </span>
  </router-link>
</template>

<script>
export default {
  computed: {
    cartAmount() {
      return !this.$store.state.cartProductsLoading
        ? this.$store.state.cartProducts.length
        : "...";
    },
  },
};
</script>
